.App {
  height: 100vh;
  width: 100vw;
  position: relative;
}

.ImgFigures {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
  background-color: #ddd;
  -webkit-perspective: 2100px;
          perspective: 2100px;
}

.ImgFigures .img-figure {
  position: absolute;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 256px;
  height: 300px;
  margin: 0;
  padding: 8px;
  cursor: pointer;
  -webkit-transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  transition: left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  -o-transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out;
  transition: transform .6s ease-in-out, left .6s ease-in-out, top .6s ease-in-out, -webkit-transform .6s ease-in-out;
  -webkit-transform-origin: 0 50% 0;
      -ms-transform-origin: 0 50% 0;
          transform-origin: 0 50% 0;
  border-radius: 3px;
  background-color: #eee;
  -webkit-box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .14), 0 3px 3px -2px rgba(0, 0, 0, .2), 0 1px 8px 0 rgba(0, 0, 0, .12);
          box-shadow: 0 3px 4px 0 rgba(0, 0, 0, .14), 0 3px 3px -2px rgba(0, 0, 0, .2), 0 1px 8px 0 rgba(0, 0, 0, .12);
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
}

.ImgFigures .img-figure.is-inverse {
  -webkit-transform: translate(256px) rotateY(180deg);
          transform: translate(256px) rotateY(180deg);
}

.ImgFigures .img-back {
  position: absolute;
  top: 0;
  left: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 16px;
  -webkit-transform: rotateY(180deg) translateZ(1px);
          transform: rotateY(180deg) translateZ(1px);
  text-align: center;
  color: #666;
  background: #eee;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;
}

.ImgFigures figcaption {
  text-align: center;
  color: #222;
}

.ImgFigures figcaption .img-title {
  font-size: 16px;
  margin: 10px 0 0 0;
}

.ControllerUnits {
  position: absolute;
  z-index: 101;
  bottom: 30px;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  -ms-flex-align: center;
      align-items: center;
  padding: 0 20%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.ControllerUnits .controller-unit {
  height: 30px;
  width: 30px;
  background-color: #aaa;
  border-radius: 50%;
  cursor: pointer;
  -webkit-transform: scale(0.5);
      -ms-transform: scale(0.5);
          transform: scale(0.5);
  -webkit-transition: background .3s, -webkit-transform .6s ease-in-out;
  transition: background .3s, -webkit-transform .6s ease-in-out;
  -o-transition: transform .6s ease-in-out, background .3s;
  transition: transform .6s ease-in-out, background .3s;
  transition: transform .6s ease-in-out, background .3s, -webkit-transform .6s ease-in-out;
}

.ControllerUnits .controller-unit.is-center {
  -webkit-transform: scale(1);
      -ms-transform: scale(1);
          transform: scale(1);
  background-color: #888;
}

.ControllerUnits .controller-unit.is-center.is-inverse {
  background-color: #108ee9;
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}


